<template>
	<view class="pages">
		<view class="content ">
			<view class="data">
				<view class="data-content">
					<view class="content-logo">
						<view class="" v-if="accounted=='admin' || accounted=='test' ||accounted=='lsm'">
						<text class="logotexts">设备异常百分比</text>
						</view>
						<view class=""v-else>
							<view class="logotexts" >{{names}}监测系统</view>
						<text class="logotext" >设备异常百分比</text>
						</view>
						
					</view>
					<view class="chartss">
						<view class="charts-box">
							<qiun-data-charts class="qiun-data-charts" type="rose" :opts="opts"
								:chartData="chartData" />
						</view>
					</view>
				</view>
				<!-- 异常选项按钮 -->
				<view class=" abnormal-list">
					<view class="list-box">
						<view class="list-contents" @click="goPolice(item.state,item.equipmentInfoList,item.count)"
							v-for="item in equipmentInfoList">
							<view class="contents-nubers" :style="[{background: item.conutBgColor}]">
								<text>{{item.count}}</text>
							</view>
							<view class="contents-names">{{item.name}}</view>
							<view class="contents-icnt">
								<image v-if="item.state ==0" class="iconimg"
									src="data:image/png;base64,"
									mode=""></image>
								<image v-else class="iconimg"
									src="data:image/png;base64,"
									mode=""></image>
							</view>
						</view>
					</view>
				</view>
				<!-- 项目列表-管理员可见 -->
				<view class="findView rowbox"   v-if="accounted=='admin' || accounted=='test' ||accounted=='lsm'">
					<view class="findTop ">
						<view class="label" > 项 目 列 表</view>
					</view>
					<view class="datalist">
						<view class="findList">
							
							<view v-for="(item,index) in lists" :key="index" class="list divflex" 
								@click="toDetialPage(item.code, item.name)">
								<!-- 左边图 -->
								<view class="lefView">
									<image :src="item.icon" class="lefimg"></image>
								</view>
								<!-- 中间 -->
								<view class="info">
									<view class="name showone">{{item.name}}</view>
								</view>
								<image class="iconimg"
									src="data:image/png;base64,"
									mode=""></image>
							</view>
						</view>
					</view>
				</view>
			
				<!-- 设备列表 用户可见-->
				<view class="findView rowbox" v-else>
					<view class="findTop ">
						<view class="label" >工 厂 节 点 </view>
					</view>
					<view class="datalist">
						<!-- 1 -->
						<view class="findList" v-if="runList.length ==1 && runList.length =='' ">
							<view v-for="(item,index) in runList" :key="index" class="list divflex" 
								@click="goDetialPages(item)">
								<!-- 左边图 -->
								<view class="lefViews">
									<image  class="lefimgs" src="data:image/png;base64,"></image>
								</view>
								<!-- 中间 -->
								<view class="info">
									<view class="name showone">{{item.name}}</view>
									<view class="name showones">测点个数:{{item.sonCount}}</view>
								</view>
								<image class="iconimg"
									src="data:image/png;base64,"
									mode=""></image>
							</view>
						</view>
						<!-- 2 -->
						<view class="findListtow" v-else>
							<view v-for="(item,index) in getrunList" :key="index" class="list divflex" 
								@click="goListfist(item)">
								<!-- 左边图 -->
								<view class="lefViews">
									<image class="lefimgs" src="data:image/png;base64," mode=""></image>
								</view>
								<!-- 中间 -->
								<view class="info">
									<view class="name showone">{{item.name}}</view>
									<view class="name showones">设备个数:{{item.sonCount}}</view>
								</view>
								<image class="iconimg"
									src="data:image/png;base64,"
									mode=""></image>
							</view>
						</view>
						
					</view>
				</view>
				
				
				
				
			</view>
		</view>
		<page-tabpars></page-tabpars>
	</view>
</template>
<script>
	import {
		mapGetters,
		mapMutations,
		getversion,
		getapkversion
	} from 'vuex'
	import uCharts from '@/components/u-chart/u-charts.js';
	import share from "@/components/share/index.vue";
	import {
		projectList,
		iconUrl,
		AbnormaList,
		nodeitemsList,
		nodeitmList,
		deviceitemsList,
		deviceList
	} from "@/api/api.js"
	export default {
		components: {
			share,
		},
		data() {
			return {
				names:"",//获取客户设备数据所需
				codes:'',//获取客户设备数据所需
				nameed:"",//点跳转时使用
				codeed:'',//点跳转时使用
				runList:[],
				getrunList:[],
				accounted:"",
				cacheListed: {},
				dangerCounts: '',
				disConnectCounts: '',
				alarmCounts: '',
				noticeCounts: '',
				data: [],
				abnorNuber: [],
				type: 0,
				chartData: {},
				opts: {
					color: ["#2fc25b", "#AC13DB", "#e65050", "#E29E09", "#EAF60D",
						"#3CA272", "#FC8452", "#9A60B4",
						"#ea7ccc"
					],
					fontColor: '#AFDDFF',
					padding: [5, 5, 5, 5],
					legend: {
						show: true,
						position: "left",
						lineHeight: 25
					},
					extra: {
						rose: {
							type: "radius",
							minRadius: 50,
							activeOpacity: 0.5,
							activeRadius: 5,
							offsetAngle: -135,
							labelWidth: 6,
							border: true,
							borderWidth: 0.5,
							borderColor: "#AFDDFF",
						}
					}
				},
				// type=0
				lists: [
					{
						icons: "../../static/logo/logo5.png"
					},
					{
						icons: "../../static/logo/logo3.png"
					},
					{
						icons: "../../static/logo/logo8.png"
					},
					{
						icons: "../../static/logo/logo4.png"
					},
					{
						icons: "../../static/logo/logo1.png"
					},
					{
						icons: "../../static/logo/logo2.png"
					},
					{
						icons: "../../static/logo/logo6.png"
					},
					{
						icons: "../../static/logo/logo9.png"
					},
					{
						icons: "../../static/logo/logo7.png"
					},
				],
				is_follow: false,

				shareData: {},
				equipmentInfoList: [{
						name: '正常设备',
						state: 0,
						count: 0,
						conutBgColor: '#2fc25b'
					},
					{
						name: '网络异常',
						state: 5,
						count: 0,
						conutBgColor: '#BB0FF0'
					},
					{
						name: '危险设备',
						state: 3,
						count: 0,
						conutBgColor: '#e65050'
					},
					{
						name: '报警设备',
						state: 2,
						count: 0,
						conutBgColor: 'orange'
					},
					{
						name: '预警设备',
						state: 1,
						count: 0,
						conutBgColor: '#E4F008'
					}
				]

			}
		},
		computed: {
			...mapGetters(['active']),
		},
		created() {},


		onLoad() {

		},




		//页面卸载时切换为竖屏配置
		onUnload() {
			// #ifdef APP-PLUS
			plus.screen.lockOrientation('portrait-primary'); //锁死屏幕方向为竖屏
			// #endif
		},
		onHide() {},
		onShow() {
			const token = uni.getStorageSync('token')
			// console.log(token,"token值")
			if (!token) {
				uni.navigateTo({
					url: '../login/index'
				})
				return
			}
			this.fetchDataList();
			this.abnorList()
			this.getServerData();
		},

		onReady() {
			this.abnorList()
			this.getServerData();
		},
		methods: {
			...mapMutations(['SET_ACTIVE']),
			

			
			// 获取公司列表
			fetchDataList() {
				let that = this
				let account = uni.getStorageSync('account')
				projectList({
					account
				}).then(res => {
					// console.log(res, 22);
					if (res.statusCode === 200) {
						that.lists = res.data
						
						this.accounted = account
					
						that.lists = that.lists.map(item => {
							// console.log(item, '测试')
							this.codes =item.code
							this.names= item.name
								// console.log(this.names,2)
							item.icon = this.iconUrl + '/src' + item.icon
							return item
							
						
						})
							this.getdeviceFists(this.codes)
							this.getdeviceList(this.codes)
					}
				})
			},
			
			
			
			
			// 获取用户数据(只有测点个数的)
				getdeviceFists(codes) {
					// let code =this.codes
			// console.log(codes,"测点")
						deviceitemsList(codes).then(res => {
									// console.log(res,"测点")
									// return
							if(res.statusCode ==200){
				           this.runList = res.data	 
							}
							
						})
					},
					
					
			// 获取用户数据(有设备的)
				getdeviceList(codes) {
			// console.log(codes,"设备个数")
						deviceList(codes).then(res => {
									// console.log(res,"设备")
									// return
							if(res.statusCode ==200){
				           this.getrunList = res.data	 
							}
							
						})
					},
			
			
			// 获取设备异常显示监测状态设备个数
			abnorList() {
				let that = this
				let account = uni.getStorageSync('account')
				AbnormaList({
					account
				}).then(res => {
					// console.log(res, 110);
					if (res.statusCode === 200) {
						that.abnorNuber = res.data
						// console.log(that.abnorNuber, 111)
						that.equipmentInfoList.map(item => {
							switch (item.state) {
								case 0:
									item.count = that.abnorNuber.normalCount;
									break;
								case 5:
									item.count = that.abnorNuber.disConnectCount;
									break;
								case 3:
									item.count = that.abnorNuber.dangerCount;
									break;
								case 2:
									item.count = that.abnorNuber.alarmCount;
									break;
								case 1:
									item.count = that.abnorNuber.noticeCount;
									break;
							}
						})
						this.dangerCounts = that.abnorNuber.dangerCount
						this.disConnectCounts = that.abnorNuber.disConnectCount;
						this.alarmCounts = that.abnorNuber.alarmCount;
						this.noticeCounts = that.abnorNuber.noticeCount
					}
				})
			},
			// uchart事件
			getServerData() {
				let that = this
				let account = uni.getStorageSync('account')
				AbnormaList({
					account
				}).then(res => {
					// console.log(res,"uchrt")
					var datas = [{
							name: '正常' + "(" + (res.data.normalCount)+ "台" + ')',
							value: res.data.normalCount,

						},
						{
							value: res.data.disConnectCount,
							name: '网络' + "(" +(res.data.disConnectCount)+ '台' + ")",

						},
						{
							value: res.data.dangerCount,
							name: '危险'+"("+(res.data.dangerCount)+'台'+")",
						},
						{
							value: res.data.alarmCount,
							name: '报警'+"("+(res.data.alarmCount)+'台'+")"
						},
						{
							value: res.data.noticeCount,
							name: '预警'+"("+(res.data.noticeCount)+'台'+")"
						},
					]
					if (res.statusCode === 200) {
						setTimeout(() => {
							let res = {
								series: [{
									data: datas
								}],
							};
							this.chartData = JSON.parse(JSON.stringify(res));
						}, 500);

					}
				})

			},


			//点击异常按钮-跳转到--police页面
			goPolice(state, dangerCounts, disConnectCounts, alarmCounts, noticeCounts) {

				dangerCounts = this.dangerCounts
				disConnectCounts = this.disConnectCounts
				alarmCounts = this.alarmCounts
				noticeCounts = this.noticeCounts
				if (state === 0) {
					return false;
				}
				if (noticeCounts === 0 && state === 1) {
					uni.showModal({
						title: "暂无数据",

					})
					return
				}
				if (alarmCounts === 0 && state === 2) {
					uni.showModal({
						title: "暂无数据",
					})
					return
				}
				if (dangerCounts === 0 && state === 3) {
					uni.showModal({
						title: "暂无数据",
					})
					return
				}
				if (disConnectCounts === 0 && state === 5) {
					uni.showModal({
					title: "暂无数据",
					})
					return
				} else {
					uni.navigateTo({
						url: `/pages/police/index?state=${state}`
					})
				}
			},
			// 项目列表
			// 点击列表项跳转
			toDetialPage(code, name) {
				nodeitmList(code,name).then(res => {
					console.log(res, 33)
					if (res.statusCode == 200) {
						if (res.data == '' || res.data.length == 1) {
							uni.navigateTo({
								url: `/pages/deviceListfist/index?code=${code}&name=${name}`
							})
						} else {
							uni.navigateTo({
								url: `/pages/deviceList/index?code=${code}&name=${name}`
							})
						}
					}

				})
			},
			
			// 设备列表(只有测点)
			// 点击列表项跳转
			goDetialPages(item){
				const code = item.code//拿到id
				const name = item.name //拿到标题名称
				const itemcode = this.code
				console.log(itemcode,3)
				console.log(code,4)
				// return
				uni.navigateTo({
					url: 	`/pages/projectDevie/index?code=${code}&name=${name}&itemcode=${itemcode}`
				})
			},
			
			// 设备列表(有设备个数)
			goListfist(item){
				const code = item.code//拿到id
				const name = item.name //拿到标题名称
				const itemcode = this.code
				console.log(itemcode,3)
				console.log(code,4)
				// return
				uni.navigateTo({
				url: `/pages/deviceListfist/index?code=${code}&name=${name}`
				})
			},
			
			
			
		},
	}
</script>

<style lang="scss" scoped>
	@import "index.scss";
	.charts-box {
		width: 98%;
		height: 200px;
		font-size: #AFDDFF;
		margin-top: 50px;
		margin-bottom: 16px;
	}
</style>
